@font-face {
    font-family: "Space_Mono_Regular";
    src: local("Space_Mono"),
        url("../../fonts/Space_Mono/SpaceMono-Regular.ttf") format("truetype");
    font-weight: normal;
}

.card-episodes {
    font-family: "Space_Mono_Regular";
    margin-top: 4px;
    margin-bottom: 25px;

}

@media (min-width: 1000px) {
    .card-episodes {
        margin-top: 10px;
    }
}

@media (max-width: 480px) {
    .card-episodes {
        text-align: center;
    }
}