.card-image {
    top: 40px;
    height: 141px;
    position: relative;
    right: 20px;

    width: 141px;
}

.card-image.inverted {
    filter: hue-rotate(-28.2deg) contrast(90%);
}

.image-container {
    position: static;

}

@media (min-width: 900px) {
    .card-image {
        height: 193px;
        width: 193px;
        right: 42px;
    }
}

@media (max-width: 480px) {
    .card-image {
        top: 20px;
        right: 0px;
    }
    .image-container {
        margin-left: auto;
        margin-right: auto;
    }
}