.mainHeader {
    align-items: center;
    display: flex;
    flex-direction: column;
    gap: 0rem;
    justify-content: center;
    min-height: 100px;
    max-width: 910px;
    margin-left: auto;
    margin-right: auto;
    position: relative;
  }

.header-container {
  background-color: #43105c;
  padding: 55px;
}

@media (max-width: 500px) {
  .header-container {
    padding-left: 0px;
    padding-right: 0px;
  }
  .mainHeader {
    max-width: 376px;
  }
}

@media (max-width: 385px) {
  .header-container {
    padding-top: 13px;
    padding-bottom: 14px;
  }
}

@media (min-width: 1000px) {
  .mainHeader{
    flex-direction:row;
    gap: 1.8rem;
  }
  .header-container {
    padding: 39px;
  }

}
