@font-face {
    font-family: "Space_Mono_Bold";
    src: local("Space_Mono"),
        url("../../fonts/Space_Mono/SpaceMono-Bold.ttf") format("truetype");
    font-weight: bold;
}


.card-title {
    font-family: "Space_Mono_Bold";
    font-size: 2.5rem;
    font-weight: bolder;
    line-height: 2.5rem;
    margin-top: 40px;
    margin-bottom: 7px;
}

.card-title.inverted {
    color: #777777;
  }

.card-title:hover {
    cursor: pointer;
}


@media (min-width: 1000px) {
    .card-title {
        margin-top: 50px;
        margin-bottom: 13px;
    }
}

@media (max-width: 480px) {
    .card-title {
        text-align: center;
    }
    
}