@font-face {
  font-family: "Space_Mono_Regular";
  src: local("Space_Mono"), url("../../fonts/Space_Mono/SpaceMono-Regular.ttf") format("truetype");
  font-weight: normal;
}
.card-author {
  display: inline;
  font-family: "Space_Mono_Regular";
  font-size: 1.16rem;
}
.card-author strong {
  color: #9c00b2;
}

strong.inverted {
  color: #fc731c;
}

@media (max-width: 480px) {
  .card-author {
    display: block;
    text-align: center;
  }
}

